*{
  margin: 0;
  padding: 0;

  box-sizing: border-box;
}

html{
  font-size: 16px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100vh;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
}

.container{
  max-width: 930px;

  height: 100%;
  max-height: 900px;

  padding: 0 15px;
  margin: 0 auto;

  overflow: hidden;
}

.calculator{
  height: 100%;
  
  margin: 0 auto;

  position: relative;

  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.calculator_wrapper{
  height: 80%;
  width: 85%;

  max-width: 500px;

  border-radius: 2rem;
  padding: 1rem;

  position: relative;
  overflow: hidden;
}


@media(max-width: 320px) {
  .calculator .key_btn{
    font-size: .6rem;
  }
}